import React from 'react';
// import axios from 'axios';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Button, Table } from 'antd';
import Loader from '../../components/Loader';

const Infralogins = () => {
	const { infralogins } = useSelector(({ projects }) => projects.userInfo);
	// const [infraLogins, setInfraLogins] = React.useState();

	// const fetchUserLogins = () => {
	// 	axios.post('/infralogins').then(({ data }) => {
	// 		setInfraLogins(data);
	// 	});
	// };

	// React.useEffect(() => {
	// 	fetchUserLogins();
	// }, []);

	const columns = [
		{
			title: 'Отображаемое имя',
			dataIndex: 'displayname',
			key: 'displayname',
		},
		{
			title: 'Логин',
			dataIndex: 'username',
			key: 'username',
		},
		{
			title: 'Пароль',
			dataIndex: 'password',
			key: 'password',
		},
	];

	return (
		<>
			<div className="controls box" style={{ padding: '14px 25px' }}>
				<b>Список твоих логинов в Infra</b>
				<Button type="primary" style={{ marginLeft: 'auto' }}>
					<Link to={'/dash'}>Назад</Link>
				</Button>
			</div>
			<div className="box">
				{!infralogins ? (
					<Loader />
				) : (
					<Table
						size="middle"
						rowKey={(record) => record.id}
						columns={columns}
						dataSource={infralogins}
					/>
				)}
			</div>
		</>
	);
};

export default Infralogins;
